import { Box, Typography } from '@mui/material';
import { QuestionItemProps } from '../../types/common';
import QuestionItem from './QuestionItem';

const getItems: () => QuestionItemProps[] = () => {
    return [
        {
            question: "   ?",
            answer: "   .         .           .   .  .1  15:00",
        },
        {
            question: "  Dress code?",
            answer: "    -    .           ",
        },
        {
            question: "    ?",
            answer: "      ,         , ,    ,    !",
        },
        {
            question: "   ?",
            answer: "  ,      ,        ,      !      ,     !",
        },
        {
            question: "   ?",
            answer: "        22.00   23.00.         23:30",
        },
        {
            question: "    ?",
            answer: "  ,  ,      - ,   . ,    !",
        },
    ]
}

const Questions = () => {
  return (
      <Box sx={{ marginTop: "5em" }}>
        <Typography sx={{ fontSize: "30.5px", marginBottom: "2em" }}></Typography>
        {
            getItems().map((item, key) => {
                return (
                    <QuestionItem answer={item.answer} question={item.question}/>
                )
            })
        }
      </Box>
  )
}

export default Questions;
